/*** 

====================================================================
	Scroll To Top style
====================================================================

***/

.back-to-top {
	position: fixed;
	right: 1.5%;
	bottom: -7.5%;
	height: 15px;
	cursor: pointer;
	width: 40px;
	padding: 3px 5px;
	font-size: 10px;
	font-weight: bold;
	color: transparent;
	text-align: center;
	z-index: 3;
	visibility: hidden;
	outline: 0 !important;
	-webkit-transition: all 0.25s ease-in-out;
	transition: all 0.25s ease-in-out;
	background-color: #434343;
}

.back-to-top::before,
.back-to-top::after {
	content: '';
	position: absolute;
	left: 0;
	width: 0;
	height: 0;
	border: 20px solid transparent;
	border-top: 0;
}

.back-to-top::before {
	top: -20px;
	z-index: 4;
	border-bottom: 20px solid #434343;
}

.back-to-top::after {
	bottom: 0;
	z-index: 5;
	border-bottom: 20px solid #ff0700;
}

.back-to-top:hover,
.back-to-top:focus {
	opacity: 1;
	text-decoration: none;
	height: 40px;
	color: #ffffff;
}

.back-to-top.show-back-to-top {
	display: block;
	bottom: 1.25%;
	visibility: visible;
}

