/*Btn Style One*/

.btn-style-one {
	position: relative;
	display: inline-block;
	font-size: 16px;
	line-height: 30px;
	color: #222222;
	padding: 10px 30px;
	font-weight: 600;
	overflow: hidden;
	border-radius: 5px;
	overflow: hidden;
	text-transform: capitalize;
	background-color: #ffffff;
	font-family: 'Archivo', sans-serif;
}

.btn-style-one:before {
	position: absolute;
	content: '';
	left: 110%;
	top: 0px;
	width: 120%;
	height: 100%;
	z-index: 1;
	opacity: 1;
	background-color: #ec3a67;
	-webkit-transition: all 0.4s;
	-moz-transition: all 0.4s;
	-o-transition: all 0.4s;
	transition: all 0.4s;
	transform: skewX(-25deg);
}

.btn-style-one .txt {
	position: relative;
	z-index: 1;
}

.btn-style-one:hover::before {
	left: 0%;
	-webkit-transform: scale(1, 1);
	transform: scale(1, 1);
}

.btn-style-one:hover {
	color: #ffffff;
}

