/* Shop Item */

.shop-item {
	position: relative;
	margin-bottom: 35px;
}

.shop-item .inner-box {
	position: relative;
	outline: 15px solid #ffffff;
	transition: all 0.3s ease;
	-moz-transition: all 0.3s ease;
	-webkit-transition: all 0.3s ease;
	-ms-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
}

.shop-item .inner-box:before {
	content: '';
	position: absolute;
	left: -15px;
	right: -15px;
	bottom: -15px;
	top: -15px;
	opacity: 0;
	z-index: 10;
	transition: all 0.3s ease;
	-moz-transition: all 0.3s ease;
	-webkit-transition: all 0.3s ease;
	-ms-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
	-webkit-box-shadow: 0px 0px 5px 0px rgba(204, 204, 204, 0.6);
	-moz-box-shadow: 0px 0px 5px 0px rgba(204, 204, 204, 0.6);
	box-shadow: 0px 0px 5px 0px rgba(204, 204, 204, 0.6);
}

.shop-item .inner-box:hover::before {
	opacity: 1;
}

.shop-item .inner-box:hover .image .overlay-box {
	opacity: 1;
}

.shop-item .inner-box .image {
	position: relative;
	z-index: 11;
	text-align: center;
	background-color: #f8f8f8;
}

.shop-item .inner-box .image .overlay-box {
	position: absolute;
	content: '';
	left: 0px;
	top: 0px;
	width: 100%;
	height: 100%;
	opacity: 0;
	text-align: center;
	background-color: rgba(19, 29, 51, 0.80);
	transition: all 0.3s ease;
	-moz-transition: all 0.3s ease;
	-webkit-transition: all 0.3s ease;
	-ms-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
}

.shop-item .inner-box .image .overlay-box .cart-option {
	position: relative;
	top: 50%;
	margin-top: -25px;
}

.shop-item .inner-box .image .overlay-box .cart-option li {
	position: relative;
	margin: 0px 3px;
	display: inline-block;
}

.shop-item .inner-box .image .overlay-box .cart-option li .tooltip-data {
	position: absolute;
	left: 20px;
	bottom: 100%;
	width: 130px;
	color: #ffffff;
	font-size: 14px;
	font-weight: 300;
	text-align: center;
	opacity: 0;
	padding: 4px 15px;
	visibility: hidden;
	margin-left: -60px;
	margin-bottom: 0px;
	background-color: #e82a6a;
	transition: all 0.3s ease;
	-moz-transition: all 0.3s ease;
	-webkit-transition: all 0.3s ease;
	-ms-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
}

.shop-item .inner-box .image .overlay-box .cart-option li .tooltip-data:after {
	position: absolute;
	content: '';
	top: 100%;
	left: 50%;
	margin-left: -8px;
	border-left: 6px solid transparent;
	border-right: 6px solid transparent;
	border-top: 8px solid #e82a6a;
}

.shop-item .inner-box .image .overlay-box .cart-option li:hover .tooltip-data {
	opacity: 1;
	visibility: visible;
	margin-bottom: 20px;
}

.shop-item .inner-box .image .overlay-box .cart-option li button {
	position: relative;
	width: 50px;
	height: 50px;
	line-height: 50px;
	text-align: center;
	color: #e82a6a;
	font-size: 18px;
	border-radius: 50%;
	display: inline-block;
	background-color: #ffffff;
	transition: all 0.3s ease;
	-moz-transition: all 0.3s ease;
	-webkit-transition: all 0.3s ease;
	-ms-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
}

